const SliderWrapper = styled.div`
        /* min-height: 950px; */
        overflow: visible;
        @media (min-width: 500px) {
          min-height: 600px;
        }
        @media (min-width: 769px) {
          min-height: 500px;
        }
`;

const RightArrow = function SampleNextArrow(props) {

    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{
                ...style,
                display: "block",
                width: "35px",
            }}

            onClick={onClick}
        />
    );
}

const NextArrow = styled(RightArrow)`
    /* position: relative; */
    background-color: transparent;
    top: 750px;
    right: 370px;

    @media (min-width: 1440px) {
        top: 758px;
        right: 500px;
  }
    @media (min-width: 2555px) {
        left: 690px;
  }
    &::before {
        content: "";
        @media (min-width: 1023px) {
            content: url(${arrowright});
        font-size: 25px;
        
        }
    }
  `;

const LeftArrow = function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{
                ...style,
                display: "block",
            }}
            onClick={onClick}
        />
    );
}

const BeforeArrow = styled(LeftArrow)`
    background-color: transparent;
    top: 750px;
    left: 372px;
    @media (min-width: 1440px) {
        top: 758px;
        left: 502px;
  }
    @media (min-width: 2555px) {
    right: 1150px;
  }
    &::before {
        content: "";
        @media (min-width: 1023px) {
            content: url(${arrowleft});
            font-size: 25px;
        }
    }
  `;


export default class SliderCarousel extends Component {
    render() {
        const settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 3,
            nextArrow: <NextArrow />,
            prevArrow: <BeforeArrow />,
            responsive: [
                {
                    breakpoint: 1025,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 769,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };


        const CarouselItem = CarouselArray.map(({ id, icon, title, text, rating }) => {
            return (
                <SliderTemplate key={id} icon={icon} title={title} text={text} rating={rating} />
            )
        })

        return (
            <SliderWrapper>
                <Slider {...settings}>
                    {CarouselItem}
                </Slider>
            </SliderWrapper>
        )
    }
}